.stak-box{
    min-height: 400px;
    border: 2px solid #F0B900;
    margin-top: 20px;
    padding: 20px;
    background-color: white;
}
.Staking{
    color: black;
    text-align: left;
    font-size: 22px;
}
.Image-Staking{
    margin-top: -10px !important;
    width: 60px;
    height: 60px;
    float: right;
}
.Staking-subheading {
    color: gray;
    text-align: left;
    font-size: 15px;
    line-height: 0.5;
    margin-bottom: 20px;
}
.border-line{
    border-bottom: 1px solid gray;
}
.font-apy{
    font-weight: bolder;
    color: black;
    
}
.Staking-Earned{
    color: #F0B900;
    font-size: 15px;
    text-align: left;
}
.Collect{
    border: none;
    background-color: #AEAEAE;
    color: white;
    width: 80px;
    float: right;
    margin-top: 30px;
    height: 32px;
    border-radius: 5px;
}
.STAKE {
    border: none;
    background-color: #AEAEAE;
    color: white;
    width: 100%;
    float: right;
    margin-top: 5px;
    height: 35px;
    border-radius: 3px;
    margin-bottom: 40px;
}
.Staking-staked {
    color: black;
    font-size: 15px;
    text-align: left;
}
.Staking-BSMX{
    color: gray;
    text-align: right;
    font-size: 15px;
}
.metamask-color {
    color: black;
    text-align: right;
    font-size: 14px;
    font-weight: 700;
}
@media screen and (max-width: 700px) {
    .Staking{
        font-size: 22px;
    }
    .Image-Staking{
        margin-top: -50px !important;
    }
    .Staking-subheading {
        font-size: 15px !important;
    }
    .Collect{
     
        margin-top: -90px;
    }
    .Staking-BSMX{
        font-size: 15px !important;
    }
    .metamask-color {
        font-size: 14px !important;
    }
    .Staking-Earned {
        font-size: 16px !important;

    }
    }


