.DetailButton {
  background-color: #a19ea4;
  color: white;
  border-radius: 17px;
  height: 35px;
  width: 150px;
  text-align: center;
  padding: 10px;
  margin-top: 25px;
  /* cursor: pointer; */
  font-size: 12px;
  font-weight: bolder;
}
.Presale {
  color: white;
  cursor: pointer;
  margin-top: -5px;
  margin-left: 10px;
  font-size: 20px;
}
.Launchpad {
  color: white;
  cursor: pointer;
  margin-top: 5px;
  margin-left: 10px;
  font-size: 12px;
}
.Softcap {
  font-size: 12px;
}
.YellowText1 {
  font-size: 12px;
  color: #fdac02;
}
.BuyButtonss {
  border-radius: 25px;
  background-color: #a19ea4;
  height: 35px;
  color: white;
  width: 380px;
  text-align: center;
  font-size: 17px;
  margin-top: 10px;
  padding-top: 5px;
  margin-left: 15%;
  cursor: pointer;
  background-image: -webkit-gradient(linear, left top, right top, from(#46bdf4), to(#2c5af5));
  background-image: -webkit-linear-gradient(left, #46bdf4, #2c5af5);
  background-image: -o-linear-gradient(left, #46bdf4, #2c5af5);
  background-image: linear-gradient(to right, #46bdf4, #2c5af5);
  padding-top: 2px;
  border: none;
  margin-bottom: 10px;
}
.setBorder > div > div {
  border: 1px solid black;
  border-radius: 25px;
}
.BuyButtonss:hover {
  /* background-color: #00C76B; */
  background-image: -webkit-gradient(linear, left top, right top, from(#46bdf4), to(#2c5af5));
  background-image: -webkit-linear-gradient(left, #46bdf4, #2c5af5);
  background-image: -o-linear-gradient(left, #46bdf4, #2c5af5);
  background-image: linear-gradient(to right, #46bdf4, #2c5af5);
}
.maxh {
  margin-left: 20px;
  font-weight: bold;
  cursor: pointer;
  padding: 10px;
  /* border: 1px solid transparent; */

  color: #2c5af5;
  border: 1px solid;
  border-radius: 5px;
}
.maxh:hover {
  color: #2c5af5;
  border: 1px solid;
  border-radius: 5px;
}
