.wallet_divs{
    padding: 10px;
    cursor: pointer;
    background-color: white;
    border-radius: 1rem;
    border: 1px solid rgb(225, 225, 225);
    font-weight: 500;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    /* min-width: 250px; */
    margin-top:14px;
    margin-left: 10px;
    margin-right: 10px;
    /* z-index: 100; */
    width: auto;
}
.wallet_divs:hover{
    background-color: #f5f5f5;
}
.wallet_divs1{
    padding-top: 16px;
    padding-left: 16px;
    background-color: white;
    font-weight: 500;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    /* min-width: 250px; */
    margin-top:4px;
}
.wallet_headings{
    /* margin: 0px 12px; */
    color: rgb(1, 1, 1);
    font-weight: 500;
    font-size: 1rem;
    margin-top: 5px;
}
.logo_img{
    width:30px;
    height:30px;

}
